import React from 'react';
import { Modal, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, Typography } from '@mui/material';

const buildData = (packages) => {
    const result = [];

    for (let i = 1; i < packages.length; i++) {
        const prevPackage = packages[i - 1];
        const currentPackage = packages[i];

        //   ,     
        const from = [...prevPackage.data];
        const to = [...currentPackage.data];

        //      JSX
        const createHighlightElements = (array, compareTo, isFrom) => {
            const maxLength = Math.max(array.length, compareTo.length);
            let elements = [];

            for (let index = 0; index < maxLength; index++) {
                const item = array[index];
                const compareItem = compareTo[index];

                if (item === undefined) { //     
                    elements.push(<span key={index} style={{textDecoration: "line-through", marginLeft:"3px"}}>Removed</span>);
                } else if (item !== compareItem) { //  
                    elements.push(<span key={index} style={{backgroundColor: "red",  marginLeft:"3px"}}>{item}</span>);
                } else { //   
                    elements.push(<span key={index} style={{marginLeft:"3px"}}>{item}</span>);
                }
            }

            return elements;
        };

        const highlightedFrom = createHighlightElements(from, to, true);
        const highlightedTo = createHighlightElements(to, from, false);

        result.push({
            time: currentPackage.time,
            from: highlightedFrom,
            to: highlightedTo
        });
    }

    return result;
};

function CustomModal({ open, handleClose, target }) {
    const data = buildData(target?.changes || []);

    let body = (
        <>
            <Typography id="modal-modal-title" variant="h6" component="h2">
                Data Changes
            </Typography>
            <Typography variant="body2" color="textSecondary">
                First appears: {target?.firstAppears}
            </Typography>
            <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                Last appears: {target?.lastAppears}
            </Typography>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 300 }} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Time</TableCell>
                            <TableCell align="left">From</TableCell>
                            <TableCell align="left">To</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {data.map((row) => (
                            <TableRow
                                key={row.time}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {row.time}
                                </TableCell>
                                <TableCell align="left">{row.from}</TableCell>
                                <TableCell align="left">{row.to}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )

    if(target?.changes.length < 2) {
        body = <>
            <Typography id="modal-modal-title" variant="h6" component="h2">
                Changes not found
            </Typography>
        </>
    }

    return (
        <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box sx={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', maxHeight: '80%',overflow:'auto', bgcolor: 'background.paper', boxShadow: 24, p: 4 }}>
                {body}
            </Box>
        </Modal>
    );
}

export default CustomModal;